$mainColor:rgb(21, 87, 109);
$borderRadius:10px;
$padding:100%;
// @import 'view/components/first-page/GymFitLogo.scss';

.App {
  text-align: center;
}

.ImageCon {
  display: flex;
}

.AppTitle{
  font-size: 50px;
}

.subTitle{
  font-style: italic;
  font-weight: bold;
  font-size: medium;
  color: red
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
